#loader-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
}
#loader {
    display: block;
    position: relative;
    left: 50%;
    top: 50%;
    width: 189px;
    height: 91px;
    margin: -45px 0 0 -94px; 
    // border-radius: 50%;
    // border: 3px solid transparent;
    // border-top-color: #3498db;

    // -webkit-animation: spin 2s linear infinite; /* Chrome, Opera 15+, Safari 5+ */
    // animation: spin 2s linear infinite; /* Chrome, Firefox 16+, IE 10+, Opera */

    z-index: 1001;

    .stroke {
      stroke: #fff;
      stroke-dasharray: 1000;
      stroke-dashoffset: 2000;
      animation: dash 6s linear infinite;
      animation-delay: 2s;
    }
    .fill {
    opacity: 0;
    animation: fill 6s linear infinite;
    animation-delay: 2s;
    }
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes fill {
  to {
    opacity: 1;
  }
}
